/* App.css */

/* Global Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
  background-color: #f7f7f7;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.UserListPage {
  text-align: center;
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.home-page h1 {
  margin-bottom: 20px;
}

.home-page li {
  margin: 10px 0;
}

.link {
  text-decoration: none;
  font-size: 18px;
  color: #007bff;
  padding: 10px 20px;
  border-radius: 5px;
  transition: background-color 0.3s;
}

.link:hover {
  background-color: #007bff;
  color: white;
}

.user-list-container {
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  padding: 20px;
  width: 300px;
  margin: 0 auto;
  max-height: 80vh; /* Ensures it doesn't exceed the viewport height */
  overflow-y: auto; /* Enables scrolling if content overflows */
}

h1 {
  margin-bottom: 20px;
  font-size: 24px;
  color: #333;
}

.user-list {
  list-style-type: none;
  padding: 0;
}

.user-item {
  margin-bottom: 15px;
  padding: 10px;
  background-color: #f1f1f1;
  border-radius: 8px;
  transition: background-color 0.3s ease;
}

.user-item:hover {
  background-color: #e3e3e3;
}

.user-name {
  font-size: 18px;
  color: #333;
  font-weight: bold;
}

.details-link {
  margin-top: 5px;
  padding: 8px 12px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.details-link:hover {
  background-color: #0056b3;
}

/* Modal Styling */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  width: 300px;
  text-align: center;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.2);
}
.modal-content p {
  line-height: 1.8; /* Adjusts the spacing between lines */
}

.close-btn {
  background-color: #dc3545;
  color: white;
  padding: 8px 12px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
}

.close-btn:hover {
  background-color: #c82333;
}
